let that;
import {
	upCommImg,
	searchCommImg,
	delCommImg
} from '@/axios/index.js'
export default {
	data() {
		return {
			service: {},
			imgurl: '',
			name: '',
			oldImgurl: '',
			oldName: '',
			id: '',
			isEdit: false
		}
	},
	onLoad() {
		that = this;
		that.searchService()
	},
	methods: {
		searchService: function() { //查询客服
			searchCommImg({
				type: 8
			}).then(res=>{
				if(res.ret == 0 && res.info.list.length) {
					console.log(res)
					that.imgurl = res.info.list[0].url
					that.oldImgurl = res.info.list[0].url
					that.name = res.info.list[0].param1
					that.oldName = res.info.list[0].param1
					that.id = res.info.list[0].id
					that.isEdit = true
				}
			})
		},
		upService: async function() { //添加客服
			if(!that.imgurl) {
				uni.showToast({
				    title: '请上传微信客服二维码',
				    duration: 2000,
					icon: 'none'
				});
				return false;
			}
			if(!that.name) {
				uni.showToast({
				    title: '请输入客服昵称',
				    duration: 2000,
					icon: 'none'
				});
				return false;
			}
			if(that.imgurl == that.oldImgurl && that.name == that.oldName) {
				return false;
			}
			console.log(111)
			if(that.id) {
				if(!await that.delService()) {
					return false
				}
			}
			that.addService()
			
		},
		edit: function() {
			that.isEdit = false
			that.name = ''
			that.imgurl = ''
		},
		changeImg: function() {
			if(that.isEdit) {
				uni.showToast({
				    title: '请点击编辑按钮后修改',
				    duration: 2000,
					icon: 'none'
				});
				return false;
			}
			uni.chooseImage({
			    count: 1, 
			    sizeType: ['original', 'compressed'],
			    sourceType: ['album'],
			    success: function (res) {
					that.imgurl = res.tempFilePaths[0]
			    }
			});
		},
		addService: function() {//添加客服
			upCommImg(that.imgurl,{
				type: 8,
				param1: that.name
			}).then(res=>{
				console.log(res)
				if(JSON.parse(res).ret == 0) {
					uni.showToast({
					    title: '保存成功',
					    duration: 2000,
						icon: 'none',
						mask:true,
						success() {
							setTimeout(res=>{
								that.$Router.back(1) 
							},2000)
						}
					});
				}
			})
		},
		delService: function() { //删除客服
			return new Promise((resolve, reject) => {
				delCommImg({
					id: that.id
				}).then(res=> {
					if(res.ret == 0) {
						
						resolve(true) 
					} else {
						resolve(false) 
					}
				})
			})
		}
	}
}
